import logo from './logo.svg';
import './App.css';
import { LoginForm } from './components/LoginForm';
import { AddPost } from './components/AddPost';
import { useContext } from 'react';
import { AuthenContext } from './components/AuthenContext';
import { BrowserRouter, Route, Routes, Navigate, Outlet, Link, HashRouter} from 'react-router-dom';
import { AddCategory } from './components/AddCategory';
import { SearchCategory } from './components/SearchCategory';
import { EditCategory } from './components/EditCategory';
import { increment, incrementAsync, incrementByAmount } from './redux/counterSlice';
import {useSelector,useDispatch } from 'react-redux';

export const Footer = () => (<h1>Footer</h1>)
const name = " DEMO"
export function Hello() {
  const save = (s) => {
    alert(`Hello ${s}`)
  }
  return <div>
    <h1>{name}</h1>
    <form>
      <input placeholder='Name' />
      <button onClick={() => save("React")}>Save</button>
    </form>
  </div>
}
//ham callback trong onClick
function App() {                                               //component(chu cai dau viet hoa)
  //JSX
  let { isAuthenticated, isLoading, logout } = useContext(AuthenContext);

  const {value}  = useSelector((state)=> state.counter)
  const dispatch = useDispatch()
  if (isLoading)
    return <div> Loading...</div>
  if (isAuthenticated){
    return (
      <>
      <div className="row">
        <button onClick={logout}>Logout</button>
        </div>
        <div className="row">
<p>Count: {value} </p>
<button onClick={() => dispatch(increment())}>increment</button>
<button onClick={() => dispatch(incrementByAmount(5))}>incrementByAmount</button>
<button onClick={() => dispatch(incrementAsync(5))}>incrementAsync</button>
        </div>
        <div className="row">
      <nav>
        <Link to="/dashboard/category/add">Add Category</Link>
        <br/>
        <Link to="/dashboard/categories">Categories</Link>
      </nav>
        <Outlet />
        </div>
        <div className="row">
        <Footer />
        </div>
      </>
    );
  }
  return 
      <Navigate to={"/login"} />;
}


export function MainApp() {
  return <BrowserRouter basename='/frontend'>
    <Routes>
      <Route path="/login" element={<LoginForm />} />

      <Route path="/dashboard" element={<App />}>
        <Route index element={<Navigate to={"/dashboard/categories"} />} />
        <Route path="category/add" element={<AddCategory />} />
        <Route path="category/:id/edit" element={<EditCategory />} />
        <Route path="categories" element={<SearchCategory />} />
      </Route>

      <Route path="/" element={<Navigate to={"/dashboard"} />} />
      <Route path="*" element={<div>404 Not Found.....</div>} />
    </Routes>
  </BrowserRouter>
}

export default App;
