import axios from 'axios';
import { useState } from 'react';
import logo from '../assets/740767.jpg';        //..  li li file 2 ln 
import './LoginForm.css';
import { useNavigate} from 'react-router-dom';
export function AddCategory({setNew}) {
    //ham hook
    let navigate = useNavigate();
    let [category, setCategory] = useState({
        name:""
    });
    
    const addNew = async () => {

        var config = {
            method: 'post',
            url: 'http://54.150.115.104:8080/api/admin/category/add',
            headers: {
                "Content-Type": "application/json",
                "Authorization": "Bearer " + localStorage.getItem("accessToken")
            },
            data: category
        }
        try {
            let response = await axios(config)
            let result = response.data
            console.log(result)
          navigate("/dashboard/categories")
        } catch (error) {
            console.log('error', error)
        }
    }
    const handleChangeName =(e) => setCategory({
        ...category,                       //ly ht tt c thuc tnh ca category nu trng name s override
        name: e.target.value
    })
    return (<>
    <h2>ADD</h2>
        <form>
            <div>
                <label>Name</label>
                <input onChange={handleChangeName}/>
            </div>
            <div>
                <button type="button" onClick={addNew}>Create</button>
            </div>
        </form>
    </>)
}

