import userEvent from '@testing-library/user-event';
import axios from 'axios';
import { useContext, useEffect, useState } from 'react';
import { CategoryContext } from './CategoryContext';
import './LoginForm.css';
import {useParams, useNavigate} from 'react-router-dom';
export function EditCategory({ setNew }) {

    let {id} = useParams();
    let navigate = useNavigate();
    console.log(id)
    //ham hook
    let [category, setCategory]= useState({id, name:''})
    
    useEffect(()=>{
        getbyId()
    },[id])

   const getbyId = async () => {
    try{
    var config = {
        method: 'get',
        url: `http://54.150.115.104:8080/api/admin/category/${id}`,
    }
      
        let response = await axios(config)
        let result = response.data
        console.log(result)
    } catch (error) {
        console.log( error)
    }
   }


    const update = async () => {

        var config = {
            method: 'put',
            url: '/admin/category/update',
            headers: {
                "Content-Type": "application/json",
                "Authorization": "Bearer " + localStorage.getItem("accessToken")
            },
            data: category
        }
        try {
            let response = await axios(config)
            let result = response.data
            console.log(result)
            navigate("/dashboard/categories",{replace:true})
        } catch (error) {
            console.log('error', error)
        }
    }
    const handleChangeName = (e) => setCategory({
        ...category,                       //ly ht tt c thuc tnh ca category nu trng name s override
        name: e.target.value
    })
    const handleChangeId = (e) => setCategory({
        ...category,                       //ly ht tt c thuc tnh ca category nu trng name s override
        id: e.target.value                       //   [e.target.name]: e.target.value    
    })
    return (<>
        <h2>UPDATE</h2>
        <div>
            <label>ID</label>
            <input onChange={handleChangeId} value={category?.id} />
            <label>Name</label>
            <input onChange={handleChangeName} value={category?.name}/>
        </div>
        <div>
            <button type="button" onClick={update}>Update</button>
        </div>

    </>)
}

