import axios from 'axios';
import { useContext, useEffect, useInsertionEffect, useState } from 'react';
import { CategoryContext } from './CategoryContext';
import './LoginForm.css';
import {Link} from 'react-router-dom';
import { useAuth } from '../hooks/useAuth';
import { deleteCategory, findCategories } from '../service/category.service';
import { useSnackbar } from 'notistack';
export function SearchCategory({idAddNew}) {
    //ham hook
   // let {setCategory: setItem} = useContext(CategoryContext)
     let {isAuthenticated, online} = useAuth();
     const { enqueueSnackbar, closeSnackbar } = useSnackbar();
    let [search, setSearchCategory] = useState({
        "keyword": "",
        "start": 0,
        "length": 10,
    });
 let[success, setSuccess] = useState(false)
    let [result, setResult] = useState({
        recordsTotal: "",
        recordsFiltered: 0,
        data: []
    });


    useEffect(() => {
        const timeout = setTimeout(()=>{
            find()
        },500)
        //goi lai
       
        return () =>clearTimeout(timeout)
    }, [search, idAddNew])

    const find = async () => {

     let{code, result} = await findCategories(search)
     if(code=200){
        setResult(result)
     }
        }
    
    const handleChangeName = (e) => setSearchCategory({
        ...search,                       //ly ht tt c thuc tnh ca category nu trng name s override
        keyword: e.target.value
    })

    const deleteItem = async (id) =>{
       let {code} = await deleteCategory(id)

       if(code==200){
        find()
        setSuccess(true)
        setTimeout(()=> setSuccess(false), 200)
        enqueueSnackbar("delete success")

       }else{
        if(code===408)
        alert("Ket noi mang")
       }
    }

    const back = () => {
        let start= search.start - search.length
        start = start < 0 ? 0 : start
        setSearchCategory({
            ...search,
            start
        })
    }

    const next = () => {
        let start= search.start + parseInt(search.length)
       if(start < result.recordsFiltered) 
        setSearchCategory({
            ...search,
            start
        })
    }
    return (<>
    <div>
        <h1>{online.toString()}-{`${isAuthenticated}`}</h1>
    </div>
        <h2>SEARCH</h2>
        <div>
            <label>Keyword</label>
            <input onChange={handleChangeName} name ="keyword"/>
            <select onChange={handleChangeName} name ="length" value={search.length}>
                <option value={5}>5</option>
                <option value={10}>10</option>
                <option value={15}>15</option>
            </select>
       
            <button type="button" onClick={back}><i className="fa-solid fa-backward"></i></button>
            <button type="button" onClick={next}><i className="fa-solid fa-forward"></i></button>
        </div>
        <table className="table">
            <thead>
                <tr>
                    <th>ID</th>
                    <th>Name</th>
                    <th>Option</th>
                </tr>
            </thead>
            <tbody>
                {
                    result.data.map(item => (
                        <tr key={item.id} >

                            <td>{item.id}</td>
                            <td>{item.name}</td>
                            <td>
                                <button className='btn btn-danger' onClick={() => deleteItem(item.id)}>
                                    <i className="fa-solid fa-trash"></i>Delete
                                </button>

                                <Link  className='btn btn-warning' to={`/dashboard/category/${item.id}/edit`}>
                                    <i className="fa-solid fa-pen-to-square"></i>Edit
                                </Link>
                            </td>
                        </tr>
                    ))
                }
            </tbody>
        </table>

    </>)
}

